<template >
  <div>
    <data-table
      :title="$t('admin_navbar_links.orders')"
      :placeholder="$t('admin_merchant.search_placeholder_orders')"
      :createPage="`/merchant/orders/create`"
      :headers="headers"
      :slots-items="['actions']"
      :isLoading="uiFlags?.isLoading"
      :items="items"
      :meta="records?.meta"
      @changePage="changePage"
      @changePerPage="changePerPage"
      @search="search"
      :hasFilter="true"
    >
      <template #filter>
        <v-row>
          <v-col md="3" cols="12">
            <div class="d-flex align-center ga-2">
              <p>{{ $t("global.started_from") }}</p>
              <date-picker
                :editable="false"
                :placeholder="$t('global.started_from')"
                v-model="filtersParams['filter[started_from]']"
                v-model:value="filtersParams['filter[started_from]']"
                type="date"
                class="mb-2 w-100"
                value-type="format"
                format="YYYY-MM-DD"
                @change="(val) => filterOrderBy(val, 'filter[started_from]')"
              />
            </div>
          </v-col>
          <v-col md="3" cols="12">
            <div class="d-flex align-center ga-2">
              <p>{{ $t("global.started_to") }}</p>
              <date-picker
                :editable="false"
                :placeholder="$t('global.started_to')"
                v-model="filtersParams['filter[started_to]']"
                v-model:value="filtersParams['filter[started_to]']"
                type="date"
                class="mb-2 w-100"
                value-type="format"
                format="YYYY-MM-DD"
                @change="(val) => filterOrderBy(val, 'filter[started_to]')"
              />
            </div>
          </v-col>
          <!-- <v-col md="3" cols="12">
            <v-autocomplete
              :placeholder="$t('admin_merchant.fields.service_provider')"
              :label="$t('admin_merchant.fields.service_provider')"
              v-model="filtersParams['filter[service_provider_id]']"
              menu-icon="mdi mdi-chevron-down"
              class="text-capitalize rounded-xl mb-2 w-100"
              :no-data-text="$t('global.actions.no_data')"
              hide-details
              outlined
              :items="SPList"
              item-title="title"
              item-value="value"
              @update:model-value="
                (val) => filterOrderBy(val, 'filter[service_provider_id]')
              "
            ></v-autocomplete>
          </v-col> -->

          <v-col md="3" cols="12">
            <!-- Main Category -->
            <v-autocomplete
              :placeholder="$t('global.main_category')"
              :label="$t('global.main_category')"
              v-model="main_category_id"
              menu-icon="mdi mdi-chevron-down"
              class="text-capitalize rounded-xl mb-2 w-100"
              :no-data-text="$t('global.actions.no_data')"
              hide-details
              outlined
              :items="mainCategoriesList"
              item-title="title"
              item-value="value"
              @update:model-value="
                (val) =>
                  getCategoriesMerchant({
                    'filter[parent_id]': val,
                    listing: 1,
                  })
              "
            ></v-autocomplete>
          </v-col>

          <v-col md="3" cols="12">
            <!-- Category -->
            <v-autocomplete
              :placeholder="$t('admin_navbar_links.categories')"
              :label="$t('admin_navbar_links.categories')"
              v-model="filtersParams['filter[main_category_id]']"
              menu-icon="mdi mdi-chevron-down"
              class="text-capitalize rounded-xl mb-2 w-100"
              :no-data-text="$t('global.actions.no_data')"
              hide-details
              outlined
              :items="categoriesList"
              item-title="title"
              item-value="value"
              @update:model-value="
                (val) => filterOrderBy(val, 'filter[main_category_id]')
              "
              :disabled="!main_category_id"
            ></v-autocomplete>
          </v-col>

          <v-col md="3" cols="12">
            <!-- Client -->
            <v-autocomplete
              :placeholder="$t('global.client_name')"
              :label="$t('global.client_name')"
              v-model="filtersParams['filter[merchant_client_id]']"
              menu-icon="mdi mdi-chevron-down"
              class="text-capitalize rounded-xl mb-2 w-100"
              :no-data-text="$t('global.actions.no_data')"
              hide-details
              outlined
              :items="clientsList"
              item-title="title"
              item-value="value"
              @update:model-value="
                (val) => filterOrderBy(val, 'filter[merchant_client_id]')
              "
            ></v-autocomplete>
          </v-col>

          <v-col md="3" cols="12">
            <!-- Branch -->
            <v-autocomplete
              :placeholder="$t('admin_navbar_links.branches')"
              :label="$t('admin_navbar_links.branches')"
              v-model="filtersParams['filter[merchant_branch_id]']"
              menu-icon="mdi mdi-chevron-down"
              class="text-capitalize rounded-xl mb-2 w-100"
              :no-data-text="$t('global.actions.no_data')"
              hide-details
              outlined
              :items="branchesList"
              item-title="title"
              item-value="value"
              @update:model-value="
                (val) => filterOrderBy(val, 'filter[merchant_branch_id]')
              "
            ></v-autocomplete>
          </v-col>

          <v-col md="3" cols="12">
            <!-- Status -->
            <v-select
              :placeholder="$t('admin_merchant.fields.status')"
              :label="$t('admin_merchant.fields.status')"
              v-model="filtersParams['filter[status]']"
              menu-icon="mdi mdi-chevron-down"
              class="text-capitalize rounded-xl mb-2 w-100"
              :no-data-text="$t('global.actions.no_data')"
              hide-details
              outlined
              :items="orderStatus"
              item-title="text"
              item-value="value"
              @update:model-value="
                (val) => filterOrderBy(val, 'filter[status]')
              "
            ></v-select>
          </v-col>
          <v-col cols="2">
            <button
              class="pa-3 rounded border text-error"
              @click="clearFilter"
              :disabled="
                !filtersParams['filter[started_from]'] &&
                !filtersParams['filter[started_to]'] &&
                !filtersParams['filter[service_provider_id]'] &&
                !filtersParams['filter[status]'] &&
                !filtersParams['filter[merchant_client_id]'] &&
                !filtersParams['filter[main_category_id]'] &&
                !filtersParams['filter[merchant_branch_id]'] &&
                !filtersParams['filter[merchant_client_id]']
              "
            >
              <v-icon size="24">mdi mdi-filter-variant-remove</v-icon>
            </button>
          </v-col>
        </v-row>
      </template>

      <template v-slot:actions="{ item }">
        <router-link
          :to="`/merchant/orders/${item.item.id}`"
          class="button button--edit px-2 rounded"
        >
          <v-tooltip :text="$t('global.actions.show')">
            <template v-slot:activator="{ props }">
              <span v-bind="props" class="mdi mdi-24px mdi-eye-outline"></span>
            </template>
          </v-tooltip>
        </router-link>
      </template>
    </data-table>
    <!-- <div class="d-md-none">
      <card-item
        :items="items"
        :meta="meta"
        :isLoading="uiFlags?.isLoading"
        @fetchItems="fetchItemScroller"
      />
    </div> -->
  </div>
</template>
<script>
import { useOrdersMerchantStore } from "@/stores/merchant/orders/orders.merchant.store";
import { mapActions, mapState } from "pinia";
import DataTable from "@/components/common/DataTable.vue";

import { useServicesMerchantStore } from "@/stores/merchant/services/services.merchant.store";
import { useCategoriesMerchantStore } from "@/stores/merchant/categories/categories.merchant.store";
import { useClientsMerchantStore } from "@/stores/merchant/clients/clients.merchant.store";
import { useBranchesMerchantStore } from "@/stores/merchant/branches/branches.merchant.store";
import CardItem from "@/components/ui/CardItem.vue";

export default {
  components: { DataTable, CardItem },
  data() {
    return {
      params: {
        "filter[keyword]": null,
        perPage: 10,
        page: 1,
        includeOrderMerchant: 1,
        includeOrderSP: 1,
        includeOrderMerchantUser: 1,
        includeOrderMerchantClient: 1,
        includeOrderMainCategory: 1,
        includeOrderAddress: 1,
        includeOrderItems: 1,
        sortAsc: 1,
      },
      filtersParams: {
        "filter[started_to]": null,
        "filter[started_from]": null,
        "filter[service_provider_id]": null,
        "filter[status]": null,
        "filter[merchant_client_id]": null,
        "filter[main_category_id]": null,
        "filter[merchant_branch_id]": null,
      },
      main_category_id: null,
    };
  },
  async mounted() {
    await this.getOrdersMerchant(this.params);
    await this.getServicesMerchant();
    await this.getMainCategoriesMerchant({ "filter[isParent]": 1, listing: 1 });
    await this.getClientsMerchant();
    await this.getBranchesMerchant();
  },
  computed: {
    ...mapState(useOrdersMerchantStore, ["records", "uiFlags", ""]),
    ...mapState(useServicesMerchantStore, {
      servicesMerchant: "records",
      uiFlagsServices: "uiFlags",
    }),
    ...mapState(useCategoriesMerchantStore, {
      categoriesMerchant: "records",
      mainCategoriesMerchant: "mainCategories",
    }),
    ...mapState(useClientsMerchantStore, {
      clientsMerchant: "records",
      uiFlagsClients: "uiFlags",
    }),
    ...mapState(useBranchesMerchantStore, {
      branchesMerchant: "records",
      uiFlagsBranches: "uiFlags",
    }),

    headers() {
      return [
        {
          title: this.$t("global.order_number"),
          align: "start",
          sortable: true,
          key: "id",
        },
        {
          title: this.$t("global.created_at"),
          align: "start",
          sortable: true,
          key: "created_at",
        },
        {
          title: this.$t("global.main_category"),
          align: "start",
          sortable: true,
          key: "main_category",
        },
        // {
        //   title: this.$t("admin_merchant.fields.title"),
        //   align: "start",
        //   sortable: true,
        //   key: "address[0].name",
        // },
        // {
        //   title: this.$t("admin_merchant.fields.phone"),
        //   align: "start",
        //   sortable: true,
        //   key: "address[0].phone",
        // },
        {
          title: this.$t("global.client_name"),
          align: "start",
          sortable: true,
          key: "client_name",
        },
        {
          title: this.$t("global.client_phone"),
          align: "start",
          sortable: true,
          key: "client_phone",
        },
        // {
        //   title: this.$t("admin_merchant.fields.service_provider"),
        //   align: "start",
        //   sortable: true,
        //   key: "service_provider",
        // },
        {
          title: this.$t("global.completed_at"),
          align: "start",
          sortable: true,
          key: "completed_at",
        },

        {
          title: this.$t("admin_merchant.fields.total"),
          align: "start",
          sortable: true,
          key: "totals.total",
        },

        {
          title: this.$t("admin_merchant.fields.status"),
          align: "start",
          sortable: true,
          key: "status",
        },
        {
          title: "#",
          align: "start",
          sortable: false,
          key: "actions",
        },
      ];
    },
    items() {
      return this.records?.data?.map((item) => {
        return {
          ...item,
          main_category: item.main_category.title
            ? item.main_category.title[this.$i18n.locale]
            : "---",
          merchant_title: item.merchant ? item.merchant.title : "---",
          client_name: item.merchant_client ? item.merchant_client.name : "---",
          client_phone: item.merchant_client
            ? item.merchant_client.phone
            : "---",
          completed_at: item.completed_at ? item.completed_at : "---",
          status: item.status
            ? this.$t(`global.order_status.${item.status}`)
            : "---",
          pick_up_type: item.pick_up_type
            ? this.$t(`global.order_type.${item.pick_up_type}`)
            : "---",
          service_provider: item.service_provider
            ? item.service_provider.title
            : "---",
        };
      });
    },
    orderStatus() {
      return [
        { value: "new", text: this.$t("global.order_status.new") },
        {
          value: "confirmed",
          text: this.$t("global.order_status.confirmed"),
        },
        {
          value: "on_the_way",
          text: this.$t("global.order_status.on_the_way"),
        },
        {
          value: "in_progress",
          text: this.$t("global.order_status.in_progress"),
        },
        {
          value: "completed",
          text: this.$t("global.order_status.completed"),
        },
        {
          value: "canceled",
          text: this.$t("global.order_status.canceled"),
        },
      ];
    },
    SPList() {
      return (
        this.servicesMerchant?.data?.map((item) => {
          return {
            value: item.id,
            title: item.title ? item.title[this.$i18n.locale] : "---",
          };
        }) || []
      );
    },
    categoriesList() {
      return (
        this.categoriesMerchant?.data?.map((item) => {
          return {
            value: item.id,
            title: item.title ? item.title[this.$i18n.locale] : "---",
          };
        }) || []
      );
    },
    mainCategoriesList() {
      return (
        this.mainCategoriesMerchant?.data?.map((item) => {
          return {
            value: item.id,
            title: item.title ? item.title[this.$i18n.locale] : "---",
          };
        }) || []
      );
    },
    clientsList() {
      return (
        this.clientsMerchant?.data?.map((item) => {
          return {
            value: item.id,
            title: item.name,
          };
        }) || []
      );
    },
    branchesList() {
      return (
        this.branchesMerchant?.data?.map((item) => {
          return {
            value: item.id,
            title: item.name,
          };
        }) || []
      );
    },
  },
  methods: {
    ...mapActions(useOrdersMerchantStore, ["getOrdersMerchant"]),
    ...mapActions(useServicesMerchantStore, ["getServicesMerchant"]),
    ...mapActions(useCategoriesMerchantStore, [
      "getCategoriesMerchant",
      "getMainCategoriesMerchant",
    ]),
    ...mapActions(useClientsMerchantStore, ["getClientsMerchant"]),
    ...mapActions(useBranchesMerchantStore, ["getBranchesMerchant"]),

    async filterOrderBy(value, key) {
      this.filtersParams[key] = value;
      await this.getOrdersMerchant({
        ...this.filtersParams,
        ...this.params,
      });
    },

    async clearFilter() {
      this.filtersParams = {
        "filter[started_to]": null,
        "filter[started_from]": null,
        "filter[service_provider_id]": null,
        "filter[status]": null,
        "filter[merchant_client_id]": null,
        "filter[main_category_id]": null,
        "filter[merchant_branch_id]": null,
        "filter[merchant_client_id]": null,
      };
      this.main_category_id = null;

      await this.getOrdersMerchant({ ...this.params });
    },

    async changePage(page) {
      this.params.page = page;
      await this.getOrdersMerchant(this.params);
    },
    async changePerPage(perPage) {
      this.params.perPage = perPage;
      this.params.page = 1;
      await this.getOrdersMerchant(this.params);
    },
    async search(text) {
      this.params["filter[keyword]"] = text;
      const key = {
        "filter[keyword]": text,
      };
      await this.getOrdersMerchant(key);
    },

    beforeToday(date) {
      return date && date > new Date();
    },
    afterToday(date) {
      return date && date < new Date();
    },

    async fetchItemScroller() {
      if (this.records?.meta?.total === this.params.page) {
        return;
      }
      // this.params.page++;
      // await this.getOrdersMerchant(this.params);
    },
  },
  watch: {
    main_category_id: {
      handler() {
        //  empty
        this.filtersParams["filter[main_category_id]"] = null;
      },
    },
  },
};
</script>
<style lang="scss" scoped>
</style>